import React, { useState, useRef } from 'react';
import { List, Avatar, Button, Skeleton, Input } from 'antd';
import { PoweroffOutlined } from '@ant-design/icons';

import AxiosRequest from '../apis';
const { Search } = Input;

// 显示顾客列表

const CustomerList = () => {
  const searchNickname = useRef('');
  const [customerList, setCustomerList] = useState([]);
  // request 数据
  const requestPage = useRef(0);
  const maxPage = useRef(1);

  const [showLoadMoreButton, setShowLoadMoreButton] = useState(true);

  const api = 'admin-wxinfo/v1/customerwxinfo';
  const method = 'get';
  const reqParams = useRef({ 'wx_nickname': '', 'page': requestPage })
  const reqObject = {}

  const [requestCount, setRequestCount] = useState(0);

  const responseDataHandle = (respObj) => {
    // 把请求加载到
    if (Object.keys(respObj).includes('code') && respObj.code === 1000) {
      let cusotmerRespomseResults = customerList.concat(respObj.results);
      setCustomerList(cusotmerRespomseResults);
      if (respObj.count && typeof (respObj.count) !== 'undefined') {
        let respMaxPage = Math.ceil(Number(respObj.count) / 10);
        maxPage.current = respMaxPage;
      } else {
        maxPage.current = 1;
      }
    } else {
      console.log('Error Request: ', respObj);
    }
  }

  const sendGetRequest = () => {
    // 发送获取顾客列表请求
    requestPage.current = requestPage.current + 1;
    if (requestPage.current > maxPage.current) {
      // requestPage > maxPage 就不发送request请求
      setShowLoadMoreButton(false);
      console.log('Not more...');
      return;
    } else {
      setShowLoadMoreButton(true);
    }
    let getParams = { 'wx_nickname': searchNickname.current, 'page': requestPage.current };
    // console.log(getParams);
    reqParams.current = getParams;
    setRequestCount(requestCount + 1);
  }

  const searchCustomerWxNickname = (nickname) => {
    // 顾客微信昵称搜寻顾客
    setCustomerList([]);
    requestPage.current = 0;
    maxPage.current = 1;
    searchNickname.current = nickname;
    sendGetRequest();
  }

  const loadMore =
    showLoadMoreButton ? (
      <div
        style={{
          textAlign: 'center',
          marginTop: 12,
          height: 32,
          lineHeight: '32px',
        }}
      >
        <Button onClick={sendGetRequest}>loading more</Button>
      </div>
    ) : null;

  return (
    <div>
      <Search placeholder="搜索顾客微信昵称" onSearch={searchCustomerWxNickname} enterButton />
      <AxiosRequest
        responseDataHandle={responseDataHandle} api={api} method={method}
        reqParams={reqParams.current} reqObject={reqObject} requestCount={requestCount}
      ></AxiosRequest><br />

      <List
        itemLayout="horizontal"
        loadMore={loadMore}
        dataSource={customerList}
        renderItem={item => (
          <List.Item
            actions={[<a key="list-loadmore-edit">查看</a>]}
          >
            {/* <Skeleton avatar title={false} loading={item.loading} active> */}
              <List.Item.Meta
                avatar={
                  <Avatar src={item.wx_avatar} />
                }
                title={`${item.wx_nickname} 住址: ${item.wx_province} ${item.wx_city}; 备注名: ${item.memo_name}`}
                description={item.explains}
              />
            {/* </Skeleton> */}
          </List.Item>
        )}
      />
    </div>
  )
}

export default CustomerList;
