import React, { useState, useEffect } from 'react';

import Layout from '../Layout';
import Header from '../Header';
import Footer from '../Footer';

import { getToken } from '../../assets/common_funcs/token_handle';

import './index.scss';
import 'antd/dist/antd.css';

import AxiosRequest from '../apis';
import { push } from 'gatsby';

const CustomLayout = ({ children }) => {
    const [requestCount, setRequestCount] = useState(0);
    const [reqParams] = useState({});
    const [reqObject] = useState({});
    const api = 'adminer/v1/admin';
    const method = 'get';
    const responseDataHandle = (respObj) => {
        // respObj 是请求结果
        if (respObj.code !== 1000) {
            // 获取管理员列表失败 跳转登陆页面
            push('/login');
        }
    }

    useEffect(() => {
        // 检查token是否有效 无效返回登陆页面
        setRequestCount(requestCount + 1);
        console.log('Token Error...');
    }, []);

    return (
        <Layout>
            <Header />
            <AxiosRequest
                responseDataHandle={responseDataHandle} api={api} method={method}
                reqParams={reqParams} reqObject={reqObject} requestCount={requestCount}
            ></AxiosRequest>
            <div className='container-div'>{children}</div>
            <Footer />
        </Layout>
    )
}

export default CustomLayout;
